const coverImages = [
    'https://lingolift-1335262060.cos.ap-guangzhou.myqcloud.com/images/bg/mc2/mc201.jpg',
    'https://lingolift-1335262060.cos.ap-guangzhou.myqcloud.com/images/bg/mc2/mc201.png',
    'https://lingolift-1335262060.cos.ap-guangzhou.myqcloud.com/images/bg/mc2/mc203.png',
];

export default function About({ lang }) {
    // 
    const locale = copy[lang] ? lang : 'en';
    const t = copy[locale];

    // 
    const [coverIndex, setCoverIndex] = useState(0);
    useEffect(() => {
        const id = setInterval(() => setCoverIndex(i => (i + 1) % coverImages.length), 6000);
        return () => clearInterval(id);
    }, []);

    return (
        <>
            {/*  1.  Parallax  */}
            <ParallaxSection
                image={coverImages[coverIndex]}
                title={locale === 'en' ? 'HKUST MC2 Lab' : ' MC2 '}
                subtitle={
                    locale === 'en'
                        ? 'Center for Metaverse and Computational Creativity'
                        : ''
                }
                isParallax
            />

            {/*  2.  Pan Hui   */}
            <PanHui lang={locale} />               {/*    */}

            {/*  3.   */}
            <section className="about-wrapper">
                <div className="about-container">
                    {t.intro.map((p, i) => (
                        <p key={i} dangerouslySetInnerHTML={{ __html: p }} />
                    ))}

                    <h3>{t.fociTitle}</h3>
                    <ul className="about-list">
                        {t.foci.map(item => (
                            <li key={item}>{item}</li>
                        ))}
                    </ul>

                    <p>{t.pubs}</p>
                    <p>{t.join}</p>
                </div>
            </section>
        </>
    );
}
