import React, { useEffect, useRef, useState } from 'react';
import { motion } from 'framer-motion';
import { SoundOutlined, SoundFilled } from '@ant-design/icons';
import { Button } from 'antd';
import './VideoSection.css';

/**
 * 
 * -------------------------------------------
 * props
 *   src            :  URL
 *   poster         : 
 *   subtitles      :  { zh: '', en: 'English' }
 *   interval       :  7500 ms
 *   threshold      : IntersectionObserver
 *   loop           : 
 *   startMuted     : 
 */
export default function VideoSection({
                                         src,
                                         poster,
                                         subtitles = [],
                                         interval = 10000,
                                         threshold = 0.5,
                                         loop = true,
                                         startMuted = true,
                                         className = ''
                                     }) {
    const videoRef = useRef(null);
    const [inView, setInView] = useState(false);
    const [muted,  setMuted ] = useState(startMuted);

    // 
    const [idx, setIdx] = useState(0);

    /* =====  ===== */
    useEffect(() => {
        const io = new IntersectionObserver(
            ([entry]) => setInView(entry.isIntersecting),
            { threshold }
        );
        if (videoRef.current) io.observe(videoRef.current);
        return () => io.disconnect();
    }, [threshold]);

    /* =====  ===== */
    useEffect(() => {
        if (!videoRef.current) return;
        inView ? videoRef.current.play() : videoRef.current.pause();
    }, [inView]);

    /* =====  ===== */
    useEffect(() => {
        if (!subtitles.length) return;
        const timer = setInterval(
            () => setIdx(prev => (prev + 1) % subtitles.length),
            interval
        );
        return () => clearInterval(timer);
    }, [subtitles, interval]);

    /* =====  ===== */
    const toggleMute = () => {
        if (!videoRef.current) return;
        videoRef.current.muted = !videoRef.current.muted;
        setMuted(videoRef.current.muted);
    };

    // 
    const current = subtitles[idx] ?? {};

    return (
        <section className={`hero-section video-section ${className}`}>
            <motion.div
                className="video-container"
                initial={{ opacity: 0 }}
                animate={{ opacity: inView ? 1 : 0 }}
                transition={{ duration: 1 }}
            >
                <video
                    ref={videoRef}
                    src={src}
                    poster={poster}
                    muted={muted}
                    loop={loop}
                    playsInline
                    preload="auto"
                    className="video-background"
                />

                {/* ----  ---- */}
                <Button
                    className="mute-button"
                    onClick={toggleMute}
                    icon={muted ? <SoundOutlined /> : <SoundFilled />}
                    shape="circle"
                    size="large"
                />

                {/* ----  ---- */}
                {!!subtitles.length && (
                    <div className="video-subtitle-container">
                        <p className="video-subtitle">{current.zh}</p>
                        <p className="video-subtitle">{current.en}</p>
                    </div>
                )}
            </motion.div>
        </section>
    );
}
